/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Crimson+Text|Barlow:400,900");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body {
  background-color: #FEFEFE;
  position: relative;
  overflow-x: hidden;
  font-family: "Crimson Text", serif; }

.app {
  text-align: center;
  padding: 25px; }

button, input[type="submit"] {
  cursor: pointer;
  padding: 12px;
  background-color: #111;
  color: #FFF;
  border: none;
  font-weight: bolder;
  border-radius: 4px;
  font-family: "Barlow", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1.25px; }

code {
  display: block;
  white-space: nowrap;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  overflow-x: auto;
  padding: 20px; }

.copy-btn {
  display: block;
  margin: 15px auto;
  width: 85%;
  max-width: 480px;
  font-size: 18px;
  background-color: #6950f7; }

.quest-details-form-wrapper {
  display: inline-block;
  width: 480px;
  margin: 12px;
  vertical-align: top; }
  @media all and (max-width: 900px) {
    .quest-details-form-wrapper {
      display: block;
      width: 95%;
      box-sizing: border-box;
      margin: auto; } }

.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.large-import {
  position: absolute;
  transform: translateY(50%) translateX(-50%);
  top: -50%;
  left: 50%; }
  .large-import input[type="file"] {
    display: none; }
  .large-import .file-import-inner {
    border-radius: 4px;
    border: dashed 3px #DDD;
    padding: 18px 36px;
    display: block;
    text-align: center;
    padding: 55px 55px;
    margin: auto; }
  .large-import button {
    display: table;
    margin: 10px auto;
    width: 100%;
    max-width: 550px; }

.file-import-inner {
  font-family: "Barlow", sans-serif;
  letter-spacing: 1.25px;
  text-transform: uppercase;
  font-weight: bolder;
  transition: border-color 0.24s ease;
  border: dashed 2px #DDD;
  cursor: pointer; }
  .file-import-inner:hover {
    border-color: #888; }

.space-between {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.top {
  font-family: "Barlow", sans-serif;
  font-weight: bolder;
  font-size: 32px;
  letter-spacing: 1.25px;
  margin-bottom: 10px; }
  .top span {
    display: inline-block;
    vertical-align: bottom; }
  .top .file-name {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
    font-size: 11px;
    padding: 0px 0px 2px;
    margin-left: 10px; }
